body {
  background-color: black;
  margin: 0;
  font-family: Arial, sans-serif;
}

.app {
  color: white;
  position: relative;
  width: 100%;
  height: 100vh;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  background-color: #333;
}

.header-left {
  display: flex;
  align-items: center;
}

.platform-text {
  color: white;
  font-size: 18px;
  font-weight: bold;
}

.header-right {
  display: flex;
  align-items: center;
}

.header-item {
  margin: 0 15px;
  display: flex;
  align-items: center;
  cursor: pointer;
}

.down-arrow {
  margin-left: 5px;
}

.profile-icon {
  color: white;
  font-size: 24px;
  cursor: pointer;
}

.and-content {
  display: none;
}

.back-to-questions {
  display: flex;
  align-items: center;
  position: absolute;
  top: 80px;
  /* Adjust the value to provide margin from the header */
  left: 20px;
  color: white;
  cursor: pointer;
}

.back-arrow {
  color: white;
  margin-right: 10px;
  font-size: 30px;
}

.back-text {
  color: white;
  font-size: 18px;
}

.content-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top: 100px;
  height: calc(100% - 160px);
}

.main-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  /* justify-content: center; */
  gap: 20px;
  max-width: 600px;
}

.box {
  background-color: white;
  color: black;
  padding: 20px;
  border-radius: 5px;

}

.box1-div1 {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.badge {
  background-color: skyblue;
  color: black;
  padding: 5px 10px;
  border-radius: 5px;
  margin-right: 10px;
}

.rocket {
  margin-left: auto;
  /* Push the rocket emoji to the right */
  font-size: 20px;
}

.box1-div2 p {
  font-size: 16px;
  font-weight: bold;
}

.box1-div3 p {
  font-size: 14px;
}

.box1-div2,
.box1-div3 {
  text-align: left;
  margin: 10px 0;
}

.icon {
  cursor: pointer;
}

.box1-div4 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 10px;
}

.views,
.info {
  display: flex;
  align-items: center;
}

.views span,
.info span {
  font-size: 10px;
}

.icon {
  width: 13px;
  margin-right: 5px;
}

.box2 {
  background-color: black;
  /* Same as body background color */
  display: flex;
  flex-direction: column;
  gap: 20px;

}

.box3 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  background-color: black;
  /* Same as body background color */
  color: white;

}

.right-section {
  display: flex;
  align-items: center;

}

.right-text {
  width: 100%;
}

.dropdownr {
  font-size: 15px;
  ;
  margin-left: 4px;
  padding: 4px;
  font-weight: bold;

  border-radius: 5px;
  border: none;
  cursor: pointer;
}

.dropdown {
  border-radius: 5px;
  border: none;
  cursor: pointer;
}

.box4 {
  background-color: white;
  color: black;
  padding: 20px;
  border-radius: 5px;
}

.box4-div1 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.like-text {
  margin-left: 3px;
  margin-right: 6px;
  font-size: 14px;
  cursor: pointer;
}

.profile-info {
  display: flex;
  align-items: center;
}

.profile-icon1 {
  color: black;
  font-size: 40px;
}

.name-date {
  display: flex;
  flex-direction: column;
  margin-left: 10px;
}

.name {
  font-weight: bold;
}

.date {
  font-size: 12px;
  color: gray;
}

.edit-section {
  display: flex;
  align-items: center;
  cursor: pointer;
}

.edit-icon {
  margin-right: 5px;
}

.box4-div2 {
  text-align: left;
  margin-bottom: 10px;
  font-size: 13px;
}

.box4-div2 p span {
  font-size: 13px;
  font-weight: bold;
  cursor: pointer;
}

.box4-div3 {
  display: flex;
  align-items: center;
}

.comment-input {
  flex-grow: 1;
  margin: 0 10px;
  padding: 5px;
  border-radius: 5px;
  border: 1px solid #ccc;
}

.post-button {
  padding: 5px 10px;
  border: none;
  border-radius: 5px;
  background-color: #007bff;
  color: white;
  cursor: pointer;
}

.box5 {
  position: fixed;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: baseline;
  height: 34vh;
  width: 24vh;
  bottom: 13vh;
  right: 16vh;
  background-color: transparent;
  color: black;
  padding: 20px;
  border-radius: 5px;

}

.firstrow {
  height: 85%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;

}

.f1 {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.text1 {
  width: fit-content;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: white;
  font-size: 13px;
  font-weight: bold;
  opacity: .8;
  padding: 4px 4px;
  border-radius: 4px;
  margin: 0 16px;
  cursor: pointer;
}

.icon1 {
  width: 38px;
  height: 38px;
  font-size: 28px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: white;
  border-radius: 50%;
  cursor: pointer;
}

.helpicon {
  font-size: 28px;

}

.secondrow {
  position: absolute;
  bottom: 5px;
  right: 20px;
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  z-index: 1000;
}

.feedback1,
.feedback3,
.feedback4 {
  position: fixed;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  height: 53vh;
  width: 35vh;
  bottom: 13vh;
  right: 14vh;
  background-color: transparent;
  color: black;
  border-radius: 5px;
  z-index: 10;
  margin-top: 20px;
}

.feedback2 {
  position: fixed;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  height: 44vh;
  width: 35vh;
  bottom: 13vh;
  right: 14vh;
  background-color: transparent;
  color: black;
  border-radius: 5px;
  z-index: 10;
  margin-top: 20px;
}

.textarea,
.textarea3,
.textarea4 {
  display: flex;
  flex-direction: column;
  background-color: #f9f9f9;
  padding: 20px;
  flex-basis: 90%;
  border-radius: 5px;
}

.textarea2 {
  display: flex;
  flex-direction: column;
  background-color: #f9f9f9;
  padding: 20px;
  flex-basis: 90%;
  border-radius: 5px;
}

.anoption,
.anoption3,
.anoption4 {
  flex-basis: 10%;
  margin-top: 10px;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  color: black;
}

.anoption2 {
  flex-basis: 10%;
  margin-top: 10px;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  color: black;
}

.feedback1-text,
.feedback3-text,
.feedback4-text {
  margin-bottom: 40px;
}

.feedback2-text {
  margin-bottom: 40px;
}

.feedback1-text span,
.feedback3-text span,
.feedback4-text span {
  font-weight: bold;
  opacity: 0.8;
}

.feedback2-text span {
  font-weight: bold;
  opacity: 0.8;
}

.feedback1-inputs,
.feedback3-inputs,
.feedback4-inputs {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.feedback2-inputs {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.section,
.section3,
.section4 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 15px;
  width: 100%;
}

.section span,
.section3 span,
.section4 span {
  font-size: 13px;
  opacity: 0.7;
  margin-bottom: 5px;
}

.dropdown,
.dropdown3,
.dropdown4 {
  width: 90%;
  padding: 10px;
  border: 1px solid #ddd;
  background-color: #ddd;
  border-radius: 5px;
}

.detail,
.detail3,
.detail4 {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 100%;
}

.detail2 {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 100%;
}

.detail span,
.detail3 span,
.detail4 span {
  font-size: 13px;
  opacity: 0.7;
  margin-bottom: 5px;
}

.detail2 span {
  font-size: 13px;
  opacity: 0.7;
  margin-bottom: 5px;
}

.input-textarea,
.input-textarea3 {
  width: 90%;
  height: 100px;
  padding: 10px;
  border: 1px solid #ddd;
  background-color: hsl(0, 0%, 87%);
  border-radius: 5px;
  margin-bottom: 10px;
  resize: none;
}

.input-textarea2 {
  width: 90%;
  height: 100px;
  padding: 10px;
  border: 1px solid #ddd;
  background-color: hsl(0, 0%, 87%);
  border-radius: 5px;
  margin-bottom: 10px;
  resize: none;
}

.bottom-row,
.bottom-row3,
.bottom-row4 {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.bottom-row2 {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.attach-button,
.attach-button3,
.attach-button4 {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  position: absolute;
  bottom: 2vh;
  left: 8px;
  background-color: #ccc;
  font-size: 13px;
  font-weight: bold;
  height: 30px;
  width: auto;
}

.attach-button2 {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  position: absolute;
  bottom: 2vh;
  left: 8px;
  background-color: #ccc;
  font-size: 13px;
  font-weight: bold;
  height: 30px;
  width: auto;
}

.attach-icon,
.attach-icon3,
.attach-icon4 {
  font-size: 14px;
}

.attach-icon2 {
  font-size: 14px;
}

.submit-button,
.submit-button3,
.submit-button4 {
  position: absolute;
  right: 0px;
  background-color: #404040;
  color: white;
  font-weight: bold;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  height: 35px;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0.7;
}

.submit-button2 {
  position: absolute;
  bottom: -60px;
