  background-color: #404040;
  color: white;
  font-weight: bold;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  height: 35px;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0.7;
}

.icon2,
.icon3,
.icon4 {
  width: 38px;
  height: 38px;
  font-size: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: white;
  border-radius: 50%;
  cursor: pointer;
  margin: 0 10px;
}

.icon22 {
  width: 38px;
  height: 38px;
  font-size: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: white;
  border-radius: 50%;
  cursor: pointer;
  margin: 0 10px;
}

.loginf2 {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.loginf2 span {
  font-size: 13px;
}

