}

@media (max-width: 550px) {
  .header {
    padding: 14px;
  }

  .platform-text {
    font-size: 14px;
  }
}

@media (max-width: 450px) {

  .back-to-questions,
  .box5,
  .header-item {
    display: none;
  }

  .content-container {
    align-items: flex-start;
    padding-top: 50px;
    height: 100%;
    margin-top: 70px;
  }

  .and-content {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-bottom: 10px;
  }

  .and1 {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    max-width: 300px;
    margin: 0 5px;
  }

  .and1 span {
    font-size: 30px;
  }

  .and2 {
    margin: 10px 5px;
  }

  .and2 span {
    font-size: 20px;
    opacity: .8;
  }

  .and31 span {
    opacity: .8;
  }

  ;

  .and1 span,
  .and2 span {
    float: left;
  }

  .and31 span {
    font-size: 20px;
  }

  .and31 {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: 0px 5px;
    margin-bottom: 5px;
  }

  .filter-button {
    display: flex;
    flex-direction: row;
    justify-content: center;
    background-color: #ddd;
    border-radius: 2px;
    align-items: center;
    margin: 0 0px;
  }

  .filter-icon {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    margin: 0 4px;
  }

  .filter-button span {
    font-size: 11px;
    font-weight: bold;

  }

  .adropdown-container {
    display: flex;
    flex-direction: column;
    gap: 0px;
    width: 100%;
  }

  .anddropdown {
    width: 100%;
    padding: 10px;
    text-align: center;
    background-color: #ddd;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 15px;
  }

  .anddropdown option {
    text-align: center;
  }

}